import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: "/",
    component: () => import("../views/MasterView/MasterView.vue"),
    children: [
      {
        path: "/dashboard",
        name: "dashboard",
        component: () => import("../views/DashboardView.vue"),
      },
      {
        path: "/userinfo",
        name: "userinfo",
        component: () => import("../views/UserInfo.vue"),
      },
    ],
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/LoginViewAlternate.vue"),
  },
  {
    path: "/userpdf",
    name: "userpdf",
    component: () => import("../templates/userProfileTemplate.vue"),
  },
  {
    path: "/:catchAll(.*)",
    name: "Not found",
    component: () => import("../components/HelloWorld.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
